export const formatAllTime = (currentTasksTime: number): string => {
  let formatTime: string = '';

  if (currentTasksTime < 60) {
    if (currentTasksTime * 10 % 10 === 0) {
      formatTime = `${currentTasksTime} `;
    } else {
      formatTime = `${currentTasksTime.toFixed(2).toString()} `;
    }

  } else if (currentTasksTime%60 !== 0) {
    formatTime = `${Math.floor(currentTasksTime / 60)}  ${(currentTasksTime%60)} `;
  } else {
    formatTime = `${Math.floor(currentTasksTime / 60)} `;
  }
  return formatTime;
}
